.actorCard {
  background-color: #343a40;
  color: whitesmoke;
  min-width: 260px;
  height: 100%;
}

.actorCard .card-header {
  background-color: #5a5e63;
}

.actorCard .card-title {
  cursor: pointer;
  margin-bottom: 0;
}

.actorCard > .card-img-top {
  width: 50%;
  margin: auto;
  padding: 20px 0;
}
